.signInScreen {
  max-width: 300px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.85);
  border-radius: 5px;
}

.signInScreen>form {
  display: grid;
  flex-direction: column;
}

.signInScreen>form>h1 {
  text-align: left;
  margin-bottom: 25px;
}

.signInScreen>form>input {
  outline-width: 0;
  height: 40px;
  margin-bottom: 14px;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
}

.signInScreen>form>button {
  padding: 16px 20px;
  font-size: 1rem;
  color: #fff;
  border: 0;
  border-radius: 5px;
  background-color: #e50914;
  font-weight: 600;
  cursor: pointer;
  margin-top: 20px;
}

.signInScreen>form>h4 {
  padding-top: 10px;
}

.singInScreen__gray {
  color: gray;
}

.signInScreen__link:hover {
  cursor: pointer;
  text-decoration: underline;
}